.main-navigation__toggle {
  position: absolute;
  content: "";
  top: 24px;
  right: 20px;
  display: block;
  min-width: 24px;
  min-height: 18px;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
.main-navigation__toggle:hover,
.main-navigation__toggle:focus {
  background-color: #f2f2f2;
}
.main-navigation--closed .main-navigation__toggle::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-navigation--closed .main-navigation__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  position: absolute;
  content: "";
  top: 8px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation--opened .main-navigation__toggle:active::before,
.main-navigation--opened .main-navigation__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .main-navigation--opened .main-navigation__wrapper {
    position: absolute;
    content: "";
    top: 64px;
    left: 0;
    z-index: 10;
    min-width: 320px;
  }
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-navigation__list {
    display: flex;
